<template>
   <div class="wallet">
       <Head data='钱包地址'></Head>
       <div class='wallet_center'>
           <div class='gross'>
               <p>总资产</p>
               <div>
                   <span class='balance'>
                       <p v-show='!flag'>{{amount}}</p>
                       <p v-show='flag'>***,**</p>
                       <em @click='hide()'></em>
                   </span>
                   <i>人民币</i>
               </div>
           </div>
           <div class='xtoken'>    
               <div v-for='(item,index) in arr' :key='index'>
                    <div class='xtoken_lt'>
                        <img :src="item.avatar" alt="">
                    </div>
                    <div class='xtoken_rt' @click='skip(item)'>
                        <div>
                            <p style='font-weight: 700; font-size:16px;'>{{item.token}}</p>
                            <p>1 {{item.token}} = ¥ {{item.price}}</p>
                        </div>
                        <div class='money'>
                            <p>{{item.amount+item.frozen}}</p>
                            <p>¥ {{item.money}}</p>
                        </div>
                    </div>
               </div>
           </div>
       </div>
   </div>
</template>

<script>

import Head from '../Head';
import axios from 'axios';

export default {
    name: 'minePig',
    components: { Head },
    data() {
        return {
            flag : false,
            arr : [],
            amount:0
        }
    },
    created(){
        this.code();
    },
    methods:{
        hide(){
            this.flag = !this.flag;
        },
        retreat(){
            this.$router.go(-1);
        },
        skip(item){
            let data = [];
            data.push(item.amount*item.frozen);
            data.push(item.token);
            console.log(data)
            this.$router.push({path:'/coin',query:{data:data}});
        },
        code(){
            let url = window.globalData.config.domain + '/user/tokenPrice';
            axios.post(url,{
                cmd:'123',
                body:{  
                    uid:localStorage.uid,
                    appid:localStorage.appid
                }
            }).then( (res) => {
                // console.log(res)
                this.arr = res.data;
                res.data.forEach( (item) => {
                    // console.log(item)
                    this.amount += item.money;
                })
            })
        }
    },

}
</script>
